.wrapper {
    padding-top: 40px;
}

.content {
    padding-top: 20px;
}

.title {
    display: flex;
    align-items: center;
    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
    color: #ffffff;
}

.title {
    svg {
        margin-right: 10px;
    }
}

.arrows {
    float: right;
    display: flex;
}

.arrow {
    cursor: pointer;
    
    &:hover {
        path {
            fill: #4C5A78;
        }
    }

    &.right {
        margin-left: 5px;
        transform: scale(-1,1);
    }
}
