.content {
    width: 76px;
    display: flex;
    height: 100%;
    background: #1a212f;
    transition: width 0.3s ease;
    flex-direction: column;
}

.show {
    display: flex;
    border-bottom: 1px solid #1F283A;
    padding: 27.5px 0;
    cursor: pointer;
    justify-content: center;

    &:hover path {
        fill: #6f7f9d;
    }
}