.tabSwitch {
    display: flex;
    justify-content: center;
    font-weight: 700;
    font-size: 14px;
    line-height: 19px;
    color: #8394B6;
    border-bottom: 1px solid transparent;
    padding-top: 7px;
    padding-bottom: 14px;
    cursor: pointer;
    width: 50%;

    &:hover {
        border-color: #222B3E;
        color: rgba(131, 148, 182,0.85);
        
        path {
            fill: rgba(131, 148, 182,0.85);
        }
    }

    svg {
        margin-right: 5px;

        path {
            fill: #8394B6;
        }
    }

    &.active {
        color: white;
        border-color: #776BF8;

        path {
            fill: #776BF8;
        }
    }
}
