@-webkit-keyframes notif-in {
    0% {
        -webkit-transform: scale(1.15);
        transform: scale(1.15);
        opacity: 0.1;
    }
    to {
        -webkit-transform: scale(1);
        transform: scale(1);
        opacity: 1;
    }
}

@keyframes notif-in {
    0% {
        -webkit-transform: scale(1.15);
        transform: scale(1.15);
        opacity: 0.1;
    }
    to {
        -webkit-transform: scale(1);
        transform: scale(1);
        opacity: 1;
    }
}

@-webkit-keyframes progress-go {
    0% {
        width: 100%;
    }
    to {
        width: 0%;
    }
}

@keyframes progress-go {
    0% {
        width: 100%;
    }
    to {
        width: 0%;
    }
}

.container {
    position: fixed;
    bottom: 30px;
    left: 30px;
    z-index: 999999;
    width: auto;
}

.wrapper {
    position: relative;
    display: flex;
    width: 310px;
    height: 77px;
    background: #1A212F;
    border-radius: 3px;
    -webkit-animation: notif-in 0.3s forwards;
    animation: notif-in 0.3s forwards;
    overflow: hidden;
    margin-top: 10px; 
    flex-direction: column;
}

.body {
    padding: 15px;
    display: flex;
    box-sizing: border-box;
    width: 100%;
    flex: 1 0 auto;
}

.body div {
    display: flex;
    width: 100%;
    align-items: center;
}

.content {
    font-weight: 700;
    font-size: 14px;
    line-height: 19px;
    color: #FFFFFF;
    margin-left: 10px;
}

.progress {
    height: 2px;
    background: #283348;
    width: 0;
    animation-name: progress-go;
    animation-timing-function: ease;
}