.points {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 20px;
    display: flex;
    justify-content: center;
}

.point {
    height: 3px;
    width: 10px;
    background: rgba(76, 90, 120, 0.7);
    border-radius: 3px;
    margin-right: 5px;
    transition: all 0.4s linear;

    &.active {
        width: 20px;
        background: rgba(119, 107, 248, 0.7);
    }
}

.point:last-child{
    margin-right: 0;
}