.modal {
    background: #121721;
    box-shadow: 0px 4px 74px rgba(0, 0, 0, 0.35);
    border-radius: 3px;
    padding: 20px 10px 20px 20px;
    width: 781px;
    height: 215px;
    flex-wrap: wrap;
    flex-direction: row
}

.title {
    display: inline-flex;
    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
    color: #ffffff;
}

.list {
    display: flex;
    position: relative;
    width: calc(100% + 15px);
    margin-top: 20px;
    height: 135px;
}

.grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 15px;
    row-gap: 15px;
    padding-right: 10px;
}

.close {
    margin-right: 8px;
}