.container {
    position: relative;
}

.selected {
    overflow: hidden;
    border-radius: 3px;
    background: #1A212F;
    display: flex; 
    cursor: pointer;
    position: relative;
    z-index: 2;
    justify-content: center;

    * {
        transition: all 0.3s;
    }
}

.arrow {
    margin-left: auto;

    svg {
        margin: 0 !important;
        
        path {
            fill: #2D435E
        }
    }
}

.open {
    box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);

    .selected {
        background: #151C28;
        border-radius: 3px 3px 0px 0px;
    }
    
    .arrow {        
        transform: scale(1, -1);

        path {
            fill: white
        }
    }
}

.list {
    animation: slide-down 0.3s forwards;
    width: 100%;
    position: absolute;
    border-radius: 0px 0px 3px 3px;
    overflow: hidden;
    box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
   
}

@keyframes slide-down {
    0% {
        opacity: 0.7; 
        height: 0;
    }

    100% {
        transform: scale(1);
        opacity: 1;
    }
}

.out {
    animation: slide-up 0.4s forwards;
}

@keyframes slide-up {
    100% {
        height: 0;
        opacity: 0.7; 
    }
}

.option {
    cursor: pointer;
    background: #1A212F;
    display: flex;
    border-top: 1px solid #1E2536;

    &:hover {
        background: linear-gradient(180deg, #1A212F 0%, #1B2231 100%);
    }
}

.option:first-child {
    border-top: 0;
}

.text {
    color: #FFFFFF;
}

.big {
    svg {
        width: 22px;
        height: 22px;
        margin-right: 10px;
    }

    .option,
    .selected {
        padding: 15px;
    }

    .text {
        font-weight: 700;
        font-size: 16px;
        line-height: 22px;
        margin-right: 7px;
    }

    .pre {
        font-weight: 600;
        font-size: 16px;
        line-height: 22px;
        color: #6B7D94;
    }

    .arrow {
        svg {
            width: 22px;
            height: 22px;
        }
    }

    .list {
        height: 210px;
    }
}

.mini {
    svg {
        width: 20px;
        height: 20px;
        margin-right: 5px;
    }

    .option,
    .selected {
        padding: 10px;
    }

    .text {
        font-weight: 600;
        font-size: 13px;
        line-height: 18px;
    }

    .arrow {
        svg {
            width: 14px;
            height: 14px;
        }
    }
}