.wrapper {
    display: flex;
    height: 100%;
    flex-direction: column;
    width: 206px;
    z-index: 2;
    background: #1a212f;
    transition: width 0.3s ease;
}

.hide {
    display: flex;
    padding: 27.5px 0 27.5px 25px;
    cursor: pointer;
    white-space: nowrap;
    overflow: hidden;

    span {
        margin: 2.5px 0 0 15px;
        display: inline-block;
        vertical-align: top;
        font-weight: 600;
        font-size: 14px;
        line-height: 19px;
        color: #6a7895;
    }

    &:hover {
        path {
            fill: #6f7f9d;
        }

        span {
            color: #6f7f9d;
        }
    }
}

.home {
    display: flex;
    border-top: 1px solid #1f283a;
    border-bottom: 1px solid #1f283a;
    padding: 11px 0;
}

.scrollBox {
    width: 100%;
    display: flex;
    flex: 1 0 auto;
}


div[data-menu='hide'] {
    width: 76px !important;
    transition: width 0.1s ease;

    span {
        display: none !important;
    }


    svg {
        display: block;
        margin: 0 auto;
    }

    .hide {
        padding-left: 0;

        &:hover {
            path {
                fill: white !important;
            }
        }
    }
}