.wrapper {
    margin-top: 20px;
    display: grid;
    grid-template-columns: auto 32.53%;
    grid-template-rows: 230px;
    column-gap: 1.204%;
}

.block {
    position: relative;
    background: #1a212f;
    border-radius: 3px;
    overflow: hidden;
    
    &:hover {
        .slide {
            opacity: 1;
        }
    }
}

.slide {
    cursor: pointer;
    position: absolute;
    display: flex;
    left: -1px;
    top: 0;
    bottom: 0;
    width: 15%;
    background: linear-gradient(90deg, #1A212F 0%, rgba(14, 18, 27, 0) 87.5%);
    opacity: 0;
    transition: opacity .2s linear;

    svg {
        margin: auto 0 auto 17px;

        path {
            fill: #6a7895b3;
        }
    }

    &.right {
        left: auto;
        right: -1px;
        transform: scale(-1,1);
    }
}