.head {
    display: flex;
    justify-content: space-between;
    padding-bottom: 40px;
}

.logo {
    display: inline-flex;
    flex: 1 1 0;
    width: 0;
    justify-content: center;
}

.welcome {
    display: inline-flex;
    margin: auto 0;
    font-weight: 700;
    font-size: 20px;
    line-height: 27px;
    color: #ffffff;
    flex: 1 1 0;
    width: 0;
}

.close {
    display: inline-flex;
    margin: auto 0;
    flex: 1 1 0;
    width: 0;
    justify-content: right;
}