.button {
    margin: auto 0;
    font-weight: 700;
    font-size: 13px;
    line-height: 18px;
    color: #FFFFFF;
    padding: 13px 29px;
    border-radius: 3px;
    cursor: pointer;
}

.grey {
    margin-left: auto;
    margin-right: 10px;
    background: #222B3E;

    &:hover {
        background: linear-gradient(180deg, #1A212F 0%, #1D2638 100%, #151B28 100%);
        color: rgb(255,255,255,0.85);
    }
}

.green {
    background: #1EA912;
    
    &:hover {
        background: #249C1A;
        color: rgba(242, 242, 242, 0.9);    
    }
}