.right {
    display: flex;
    margin: auto 0 auto auto;
    align-items: center;
    
    a:hover {
        opacity: 0.85;
    }
}

.email {
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: #6A7895;
    margin-left: 5px;
}

.links {
    display: flex;
    margin-left: 40px;
    margin-right: 40px;

    a {
        display: flex;
        margin-left: 20px;

        &:hover path {
            fill: rgba(106, 120, 149, 0.8509803922);
        }
    }
}
