.rowHead {
    padding: 0 15px 20px 15px;
    display: grid;
}

.row {
    display: grid;
    padding: 15px;
    background: #1a212f;
    border-radius: 3px;
}

.animationOnAdd {
    animation: add-row 0.3s forwards;
}

@keyframes add-row {
    from {
        opacity: 0.1;
    }

    to {
        opacity: 1;
    }
}

.colorBack {
    background: transparent;
}

.head {
    font-weight: 600;
    font-size: 12px;
    line-height: 16px;
    color: #8394b6;
}

.column {
    font-weight: 400;
    font-size: 14px;
    line-height: 19px;

    svg {
        vertical-align: top;
        width: 19px;
        height: 19px;
        margin-right: 10px;
    }
}

.column {
    &:last-child {
        text-align: right;
    }
}

.white {
    color: #ffffff;
}

.grey {
    color: #8394b6;
}

.green {
    color: #1ea912;
    font-weight: 500;
}

.red {
    color: #e92121;
    font-weight: 500;
}

.center {
    text-align: center;
}
