.loginDirectly {
    display: flex;
    padding: 20px 0;
    background: #1A212F;
    box-shadow: 0px 4px 81px rgba(47, 41, 52, 0.25);
    font-weight: 600;
    font-size: 14px;
    line-height: 19px;
    color: #6A7895;
    justify-content: center;
    align-items: center;
}

.loginDirectly span {
    cursor: pointer;
    display: inline-flex;
    margin-left: 15px;

    &:hover {
        opacity: 0.85;
    }
}