.modal {
    background: #121721;
    box-shadow: 0px 4px 74px rgba(0, 0, 0, 0.35);
    border-radius: 3px;
    flex-wrap: wrap;
    width: 404px;
}

.ifLost {
    margin-top: 20px;
    font-weight: 400;
    font-size: 14px;
    line-height: 19px;
    text-align: center;
    color: #6a7895;
}

.email {
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #ffffff;
    margin-top: 10px;
    text-align: center;

    svg {
        margin-right: 5px;
        vertical-align: top;
        
        path {
            fill: #776bf8;
        }
    }
}
