.wrapper {
    border-bottom: 1px solid transparent;
    box-sizing: border-box;
    padding: 17.5px 0;
    transition: background-color 0.3s linear;

    &.isAuth {
        background: #121721;
        border-color: #1A212F;
    }
}

.content {
    flex-direction: row;
}

.logo {
    display: inline-flex;
    margin-top: auto;
    margin-bottom: auto;
    width: 149px;
    height: 44px;

    &:hover {
        opacity: 0.85;
    }
}

.links {
    display: inline-flex;
    margin-left: 78px;
}

.link {
    display: inline-flex;
    margin-right: 25px;
    font-weight: 600;
    font-size: 14px;
    line-height: 19px;
    color: #FFFFFF;
    text-decoration: none;
    margin-top: auto;
    margin-bottom: auto;

    &:hover {
        opacity: 0.85;
    }
}