.wrapper {
    width: 100%;
    height: 100%;
    position: fixed;
    display: flex;
    z-index: 1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
}

.content {
    display: flex;
    width: 404px;
    margin: auto;
    flex-direction: column;
}

.form {
    background: #121721;
    border-radius: 3px;
    overflow: hidden;
}

.logo {
    cursor: pointer;
    width: 149px;
    height: 40px;
    margin: 0 auto 40px auto;
}
