.form {
    display: flex;
    flex-direction: column;
    margin-bottom: auto;
    border-radius: 3px;
}

.title {
    display: flex;
    background: #1A212F;
    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
    color: #FFFFFF;
    padding: 20px 0 20px 20px;
    align-items: center;
    border-radius: 3px 3px 0 0;

    svg {
        margin-right: 10px;
        width: 22px;
        height: 22px;

        path {
            fill: #776BF8;
        }
    }
}

.fillDetails {
    font-weight: 500;
    font-size: 14px;
    line-height: 19px;
    color: #8394B6;
    padding-bottom: 5px;
}

.box {
    display: flex;
    flex-direction: column;
    padding: 20px;
}