.checkBox {
    display: flex;
    background: #222b3e;
    padding: 13px 15px;
    cursor: pointer;
    border-radius: 0px 0px 3px 3px;

    svg {
        margin-left: auto;
    }
}

.goodParent {
    background: #1a212f;
}

.text {
    padding: 2px 0;
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    color: #8394b6;
}
