
.button {
    padding: 20px;
    background: #1a212f;
    border-radius: 3px;
    box-sizing: border-box;
    display: inline-flex;
    cursor: pointer;

    &:nth-child(3n) {
        margin-right: 0;
    }

    &:nth-child(1),
    &:nth-child(2),
    &:nth-child(3) {
        margin-top: 0;
    }
    &:hover {
        background: linear-gradient(180deg, #1a212f 0%, #1d2638 100%, #151b28 100%);

        .flag,
        .name {
            opacity: 0.85;
        }
    }
}

.name {
    display: inline-flex;
    font-weight: 600;
    font-size: 14px;
    line-height: 19px;
    color: #ffffff;
    margin-left: 10px;
}

.flag {
    display: inline-flex;
}

.tick {
    display: inline-flex;
    margin-left: auto;
    width: 19px;
    height: 19px;
}

.selected {
    background: #232e43 !important;
    cursor: default !important;

    .flag,
    .name {
        opacity: 1 !important;
    }
}
