.big {
    width: 100% !important;
}

.box {
    display: flex;
    padding: 20px 30px;
    margin-bottom: 15px;
    background: #1a212f;
    border-radius: 3px;
    flex-direction: column;
    align-items: center;
    
    svg {
        display: flex;
        width: 50px;
        height: 50px;
        
        path {
            fill: #1EA912;
        }
    }
}

.text {
    margin-top: 10px;
    font-weight: 600;
    font-size: 14px;
    line-height: 19px;
    text-align: center;
    color: #ffffff;
}