.buttons {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 15.5px;
    border-top: 1px solid #1f283a;
    box-sizing: border-box;
}

.button {
    cursor: pointer;
    width: 100%;
    background: #222b3e;
    border-radius: 3px;
    margin: 0 auto;
    box-sizing: border-box;
    padding: 15px;
    white-space: nowrap;
    overflow: hidden;

    span {
        padding: 2px 0 0 10px;
        display: inline-block;
        vertical-align: top;
        font-weight: 600;
        font-size: 14px;
        line-height: 19px;
        color: #6a7895;
    }

    svg {
        width: 23px;
        height: 23px;
    }

    &:hover {
        background: linear-gradient(180deg, #222b3e 0%, #273148 100%);

        span {
            color: #8394b6;
        }
    }
}

div[data-menu='hide'] {
    .buttons {
        padding: 15px 11px;
    }
}