.button {
    cursor: pointer;
    display: flex;
    background: #776BF8;
    border-radius: 3px;
    font-weight: 700;
    font-size: 14px;
    line-height: 19px;
    padding: 8px 10px;    
    color: #FFFFFF;
    justify-self: center;
    margin-left: 10px;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 81px;
    white-space: nowrap;
    
    &:hover {
        background: #7165EA;
        color: rgba(255,255,255,0.85);

        svg {
            animation: shake 0.4s cubic-bezier(.36,.07,.19,.97) both;
            opacity: 0.85;
        }
    }

    svg {
        margin-right: 5px;
    }

    &.disabled {
      cursor: default !important;
      color: white !important;
      background: #222B3E !important;
    }
}

@keyframes shake {
    10%, 90% {
      transform: translate3d(-0.5px, 0, 0);
    }
    
    20%, 80% {
      transform: translate3d(1.5px, 0, 0);
    }
  
    30%, 50%, 70% {
      transform: translate3d(-3px, 0, 0);
    }
  
    40%, 60% {
      transform: translate3d(3px, 0, 0);
    }
  }