.modal {
    display: flex;
    box-sizing: border-box;
    margin: auto;
    flex-direction: column;
    -webkit-animation: modal-in 0.3s forwards;
    animation: modal-in 0.3s forwards;
}

.out {
    -webkit-animation: modal-out 0.3s forwards;
    animation: modal-out 0.3s forwards;
}

.overlay {
    position: fixed;
    display: flex;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 3;
    background: linear-gradient(180deg, rgba(5, 7, 11, 0.9) 8.06%, #0e121b 100%);
    backdrop-filter: blur(2px);
}

@-webkit-keyframes modal-out {
    0% {
        -webkit-transform: scale(1);
        transform: scale(1);
        opacity: 1;
    }
    to {
        -webkit-transform: scale(1.15);
        transform: scale(1.15);
        opacity: 0.1;
    }
}

@keyframes modal-out {
    0% {
        -webkit-transform: scale(1);
        transform: scale(1);
        opacity: 1;
    }
    to {
        -webkit-transform: scale(1.15);
        transform: scale(1.15);
        opacity: 0.1;
    }
}

@-webkit-keyframes modal-in {
    0% {
        -webkit-transform: scale(1.15);
        transform: scale(1.15);
        opacity: 0.1;
    }
    to {
        -webkit-transform: scale(1);
        transform: scale(1);
        opacity: 1;
    }
}

@keyframes modal-in {
    0% {
        -webkit-transform: scale(1.15);
        transform: scale(1.15);
        opacity: 0.1;
    }
    to {
        -webkit-transform: scale(1);
        transform: scale(1);
        opacity: 1;
    }
}