.input {
    display: flex;
    width: 100%;
    margin-top: 10px;
    border-radius: 3px;
    background: #19202E;
    padding: 0 15px;
    box-sizing: border-box;
    align-items: center;
    
    &:hover {
        background: linear-gradient(180deg, #1A212F 0%, #1B2231 100%);
    }

    input {
        font-weight: 600;
        font-size: 14px;
        line-height: 19px;
        color: #FFFFFF;
        outline: unset;
        border: 0;
        padding: 20px 0;
        background: transparent;
        flex: 1 0 auto;
        letter-spacing: 0.4px;
        width: 0;

        &::placeholder {
            font-weight: 400;
            opacity: 1;
            color: #37435A;
        }
    }

    &.checked {
        border-radius: 3px 3px 0px 0px;
    }

    &.fill .icon path {
        fill: white !important;
    }

    &.good {    
        background: #222B3E;
    }

    &.bad {    
        opacity: 1;
    }
}

.icon {
    display: inline-flex;
    margin-right: 10px;

    svg {
        width: 19px;
        height: 19px;
    }

    path {
        fill: #51607C;
    }
}