.thumb {
    background: #283348;

    &:hover {
        background: #2f3b53;
    }
}

.horizontal {
    height: 3px !important;
}

.vertical {
    width: 3px !important;
}

.horizontalTrack {
    height: 3px !important;
}

.horizontalPos {    
    right: 2px;
    bottom: 2px;
    left: 2px;
}

.verticalTrack {
    width: 3px !important;
}

.verticalPos {
    right: 2px;
    bottom: 2px;
    top: 2px;
}

.zeroVerticalPos {
    right: 0;
    bottom: 0;
    top: 0;
}

.zeroHorizontalPos {
    right: 0;
    bottom: 0;
    left: 0;
}