.button {
    display: inline-flex;
    cursor: pointer;
    border: 1px solid #222B3E;
    border-radius: 3px;
    box-sizing: border-box;
    padding: 15px 0;
    font-weight: 700;
    font-size: 14px;
    line-height: 19px;
    color: #FFFFFF;
    justify-content: center;
    
    &:hover {
        background: rgba(34, 43, 62, 0.25);
    }

    &:first-child {
        width: 140px;
        margin-right: 10px;
    }

    &:nth-child(2) {
        width: 212px;
    }

    &.big {
        width: 100%;
    }

    svg {
        width: 19px;
        height: 19px;
        margin-right: 5px;
    }
}

.green {
    background: #1EA912;

    &:hover {
        background: #249C1A;
        color: rgba(242, 242, 242, 0.9);

        svg {
            opacity: 0.9;
        }
    }
}

.big {
    margin-top: 15px;
    width: 100%;
}