
.list {
    display: flex;
    flex-direction: column;
    padding-right: 85px;
    
    &:last-child {
        padding-right: 0;
    }
}

.title {
    display: flex;
    font-weight: 600;
    font-size: 14px;
    line-height: 19px;
    color: #FFFFFF;
    padding-bottom: 5px;
    text-transform: uppercase;
}