.content {
    display: flex;
    padding: 15px;
    box-sizing: border-box;
    width: 100%;
    background: #222b3e;

    &::before {
        content: "";
        display: block;
        position: absolute;
        background: linear-gradient(180deg, rgba(14, 18, 27, 0.8) 0%, rgba(14, 18, 27, 0) 100%);
        width: 100%;
        height: 50px;
        bottom: 0;
        left: 0;
        margin-bottom: -50px;
    }

}

.hide {
    display: flex;
    margin-left: auto;
    cursor: pointer;
    padding: 13px 0 13px 15px;

    svg{
        transform: scale(-1, 1);
    }

    &:hover path {
        fill: #6f7f9d;
    }
}