.link {
    display: block;
    width: 100%;
    padding: 6px 1px 6px 24px;
    border-left: 1px solid transparent;
    box-sizing: border-box;
    white-space: nowrap;
    overflow: hidden;
    transition: border 0.7s ease;

    svg {
        width: 23px;
        height: 23px;
    }

    span {
        margin: 2.5px 0 0 15px;
        display: inline-block;
        vertical-align: top;
        font-weight: 400;
        font-size: 14px;
        line-height: 19px;
        color: #9ba5ba;
        transition: padding 0.4s ease;
    }

    &:hover {
        border-color: #8394b6;

        path {
            fill: #8394b6;
        }

        span {
            font-weight: 500;
            color: #8394b6;
            padding-left: 3px;
        }
    }
}

.isActive {
    cursor: default !important;
    border-color: #776bf8;

    path {
        fill: #776bf8 !important;
    }

    span {
        font-weight: 600 !important;
        color: #ffffff !important;
        padding-left: 0 !important;
    }
}

.link {
    margin-bottom: 12px;
}

.link:last-child{
    margin-bottom: 0;
}

div[data-menu='hide'] {
    .link {
         padding-left: 0;
    }
}